import React from 'react';
import Link from 'next/link';

export const NoTripView = () => {
    return (
        <div className="flex flex-col items-center justify-center min-h-[60vh] text-center p-8 space-y-6">
            <div className="max-w-2xl space-y-4">
                <h1 className="text-4xl font-serif text-slate-900">Morocco Awaits</h1>
                <p className="text-lg text-slate-600">
                    You don't have any bookings yet. Start your journey by exploring our curated experiences.
                </p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 w-full max-w-4xl mt-8">
                {/* Placeholders for featured experiences */}
                {[1, 2, 3].map((i) => (
                    <div key={i} className="group relative aspect-[4/5] bg-slate-100 rounded-2xl overflow-hidden cursor-pointer">
                        <div className="absolute inset-0 bg-slate-200 animate-pulse" />
                        <div className="absolute bottom-0 left-0 right-0 p-6 bg-gradient-to-t from-black/60 to-transparent">
                            <span className="text-white font-medium text-lg">Experience {i}</span>
                        </div>
                    </div>
                ))}
            </div>

            <Link
                href="/experiences"
                className="mt-8 px-8 py-3 bg-emerald-600 text-white rounded-full font-medium hover:bg-emerald-700 transition-colors"
            >
                Explore Experiences
            </Link>
        </div>
    );
};
